.BlogBlock{
    padding: 150px 0;
}
.blog-card{
    height: 263px;
    padding: 30px;
    background-size: cover;
    background-position: center;
    margin-bottom: 30px;
    color: #fff;
}
.blog-card-title{
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 100%;
    margin-bottom: 60px;
}
.blog-card-description{
    font-family: Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 140%;
}