.RentBlock{
    padding: 80px 0;
    margin-bottom: 150px;
}
.rent-card{
    border-radius: 5px;
    .rent-card-image{
        height: 233px;
        position: relative;
        background-size: cover;
        background-position: center;
        border-radius: 5px 5px 0px 0px;
        &:before{
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background: linear-gradient(180deg, #323232 0%, rgba(100, 100, 100, 0) 100%);
            border-radius: 5px 5px 0px 0px;
            z-index: 0;
        }
    }
    .rent-card-title{
        position: relative;
        font-family: Roboto;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 100%;
        color: #FFFFFF;
        padding: 30px;
        z-index: 1;
    }
    .rent-card-body{
        background-color: #fff;
        padding: 30px;
        
        border-radius: 0px 0px 5px 5px;
    }
    .rent-card-price{
        margin-bottom: 15px;
    }
    .rent-card-price-text{
        font-family: Roboto;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 140%;
        color: #757575;
    }
    .rent-card-price-value{
        font-family: Roboto;
        font-style: normal;
        font-weight: 500;
        font-size: 22px;
        line-height: 140%;
        color: #212121;
    }
    .rent-card-tech{
        display: flex;
        align-items: center;
        margin-bottom: 5px;
    }
    .rent-card-tech-icon{
        font-size: 24px;
        color: #979797;
        line-height: 1;
    }
    .rent-card-tech-text{
        font-family: Roboto;
        font-style: normal;
        font-weight: 300;
        font-size: 12px;
        line-height: 160%;
        color: #212121;
        margin-left: 10px;
    }
}