.TextCards{
    padding: 215px 0;
    background-size: cover;
    background-position: center;
}
.TextCard{
    background-color: #fff;
    border-radius: 4px;
    padding: 30px;
    height: 100%;
    &-title{
        font-family: Roboto;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 100%;
        color: #212121;
        margin-bottom: 10px;
    }
    &-description{
        font-family: Roboto;
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 140%;
        color: #000000;
    }
}